<!-- 注册 -->
<template>
	<div>
   <div>
    <!-- 使用navbar组件 -->
    <Navbar></Navbar>
  </div>
  <div class="register-background">
  <div >
    <van-cell  class="name">
      <!-- 输出用户名 -->
      <van-field
      v-model="input_name"
      label="用户名"
      left-icon="friends"
      right-icon="arrow-left"
      placeholder="请输入用户名"
      @blur="check_name"
     
      />
    </van-cell>
      <div>
        <!-- 用户名是否被注册 -->
        <van-icon v-if="code==5" name="clear" >该用户名已被注册</van-icon>
        <van-icon v-if="code==1" name="checked" >该用户名可用</van-icon>
      </div>
      <!-- 输入密码 -->
      <van-cell  class="password">
      <van-field
      v-model="input_password"

      clearable
      label="密码"
      left-icon="star"
      right-icon="arrow-left"
      placeholder="请输入密码"
      />
    </van-cell>
  </div>
  <div >
    <van-button  class="zhuce" round type="primary" color="linear-gradient(to right, #00ffff, #0080ff)" @click="register">注册</van-button>
  </div>
</div>
</div>
</template>
<script>
	import axios from 'axios';
	import Navbar from '../components/Navbar.vue';
  import { Dialog } from 'vant';
  export default {
    name: "Register",
    components: {
     Navbar,
   },

   data(){
     return {
      cheakname:'',
      code:"",
  		username:"",//登录成功后获取的名字
  		input_name:"",
  		input_password:"",
  	}
  },
  created(){
  },
  methods:{
    check_name()
    {
      axios.get('/php/user/r_check.php', {
        withCredentials:true,
        params:{
          username: this.input_name,}
        }).then(res=>{
          this.code = res.data.code;
        })
        .catch(error=>{
      Dialog.alert({
    title: '网络错误',
    message: error,
  }).then(() => {
    return;
}); 
        });
      },
      register()
      { 
        let data = new FormData();
data.append('username',this.input_name);
data.append('password',this.input_password);
       axios.post('/php/user/resister.php', 
        data,).then(res=>{
         this.code = res.data.code;
         this.username = res.data.data;
         if(this.code==1)
         {
           Dialog.alert({
            title: '恭喜！！！',
            message: '注册成功！！！',
          }).then(() => {
            this.$router.push({name:'Home'})

  // on close
});
        }
        if(this.code==0)
        {
          Dialog.alert({
            title: '抱歉',
            message: '注册失败',
          }).then(() => {
           

  // on close
});
        }
      })
        .catch(error=>{
        Dialog.alert({
    title: '网络错误',
    message: error,
  }).then(() => {
    return;
}); 
        });
      }
    }
  }
</script>

<style scoped>

  .register-background{
    position: absolute;
    height: 100%;
    width: 100%;
    background-size: cover;
   background-color:  #AFEEEE;
  }

 .name{
    position:relative;
 width: 90%;
 left: 5%;
 margin-top:40%;
 padding: 2%;
 }
.password{
   width: 90%;
  position:relative;
 left: 5%;
 margin-top:10%;
 padding: 2%;
}
  .zhuce{
  position: relative;
 left: 40%;
 margin-top: 20%;
 width: 20%;
  }
</style>